* {
	margin: 0;
	padding: 0;
}

ul, li {
	list-style: none;
}

.kakao_wrap {
	width: 1200px;
	margin: 0 auto;
	padding-top: 50px;
}

.kakao_list {
	display: flex;
	flex-wrap: wrap;
}

.kakao_list li {
	flex: 1;
	margin-top: 15px;
    box-sizing: border-box;
}

.kakao_list li h1 {
	color: #fee500;
}

.kakao_list li h3 {
	color: #000;
}

.kakao_list li .img_wrap {
    display: flex;
	width: 130px;
    height: 130px;
	margin-top: 10px;
    align-items: center;
    justify-content: center;
}

.kakao_list li .img_wrap img {
	max-height: 100%;
    max-width: 100%;
}

.loading {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50% -50%);
}

.MuiPagination-root {
	margin: 30px 0;
}

.MuiPagination-root .MuiPagination-ul {
	justify-content: flex-end;
	flex-wrap: nowrap;
}

.MuiPagination-root .MuiPagination-ul button {
	min-width: 28px;
    height: 28px;
}

@media screen and (max-width: 1024px) {
	.kakao_wrap {
		width: 500px;
	}
}

@media screen and (max-width: 640px) {
	.kakao_wrap {
		width: 300px;
	}
}